import { ThemeProvider } from "@material-ui/core";
import React from "react";

import theme from "./theme/Theme";
import NavBar from "./Components/Navbar/NavBar.js";
import MainSlide from "./Components/Sliders/MainSlide";
import { makeStyles } from "@material-ui/core/styles";
import CssBaseline from "@material-ui/core/CssBaseline";
import Footer from "./Components/Footer/Footer";
import MiniSlider from "./Components/Sliders/MiniSlider";

import Scroll from "./Components/General/Scroll";
const useStyles = makeStyles((theme) => ({
  root: {
    // minHeight: "100vh",
    // backgroundImage: `URL("https://images.unsplash.com/photo-1621888571553-569e36633e7b?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1834&q=80")`,
    // backgroundRepeat: "no-repeat",
    // backgroundSize: "cover",
  },
}));

const App = (props) => {
  const classes = useStyles();

  return (
    <>
      <ThemeProvider theme={theme}>
        <CssBaseline>
          <div className={classes.root}>
            <NavBar />
            <MainSlide />

            <MiniSlider />
            <Footer />
            <Scroll showBelow={250} />
          </div>
        </CssBaseline>
      </ThemeProvider>
    </>
  );
};

export default App;
